import ifDefined from '@ui5/webcomponents-base/dist/renderer/ifDefined.js';
import { html, svg, repeat, classMap, styleMap } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';
const block0 = (context) => { return html`<tr class="ui5-table-row-root">	${ repeat(context.visibleCells, undefined, (item, index) => block1(item, index, context)) }</tr>${ repeat(context.popinCells, undefined, (item, index) => block2(item, index, context)) }`; };
