
/* eslint no-unused-vars: 0 */
import ifDefined from '@ui5/webcomponents-base/dist/renderer/ifDefined.js';
import { html, svg, repeat, classMap, styleMap } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';
const block0 = (context) => { return html`<div	data-sap-ui-fastnavgroup="true"	class="ui5-panel-root"	role="${ifDefined(context.accRole)}"><!-- header: either header or h1 with header text --><header		@click="${ifDefined(context._headerClick)}"		@keydown="${ifDefined(context._headerKeyDown)}"		@keyup="${ifDefined(context._headerKeyUp)}"		class="ui5-panel-header"		tabindex="${ifDefined(context.headerTabIndex)}"	><div			class="ui5-panel-header-button-root"		><ui5-button				design="Transparent"				class="ui5-panel-header-button"				aria-expanded="${ifDefined(context.expanded)}"				?non-focusable="${ifDefined(context.nonFocusableButton)}"				@click="${ifDefined(context._toggleButtonClick)}"			><ui5-icon					src="sap-icon://navigation-right-arrow"					class="ui5-panel-header-button-icon"					accessible-name="${ifDefined(context._icon.title)}"				></ui5-icon></ui5-button></div>		${ context._hasHeader ? block1(context) : block2(context) }</header><!-- content area --><div class="ui5-panel-content" tabindex="-1" style="${ifDefined(styleMap(context.styles.content))}"><slot></slot></div></div>`; };
const block1 = (context) => { return html`<slot name="header"></slot>		`; };
const block2 = (context) => { return html`<div class="ui5-panel-header-content"><h1 class="ui5-panel-header-title">${ifDefined(context.headerText)}</h1></div>		`; };

export default block0;